.location > .panel {
  position: relative;
  margin-bottom: 30px;
}
.location > .panel .panel-body .panel {
  -webkit-transition: border-color ease-in-out 0.4s, -webkit-box-shadow ease-in-out 0.4s;
  -o-transition: border-color ease-in-out 0.4s, box-shadow ease-in-out 0.4s;
  transition: border-color ease-in-out 0.4s, box-shadow ease-in-out 0.4s;
}
.location > .panel .panel-body .panel > .panel-heading {
  -webkit-transition: background-color ease-in-out 0.4s, -webkit-box-shadow ease-in-out 0.4s, border-color ease-in-out 0.4s;
  -o-transition: background-color ease-in-out 0.4s, box-shadow ease-in-out 0.4s, border-color ease-in-out 0.4s;
  transition: background-color ease-in-out 0.4s, box-shadow ease-in-out 0.4s, border-color ease-in-out 0.4s;
}
.location > .panel .overlay {
  position: absolute;
  pointer-events: none;
  width: 100%;
  height: 100%;
  background-color: #000;
  display: none;
}
.location > .panel .overlay .content-wrapper {
  height: 100%;
  width: 100%;
  color: white;
}
.location > .panel .overlay .content-wrapper .booking-progress {
  display: none;
  position: absolute;
  width: 100%;
  top: 50%;
  margin-top: -50px;
}
.location > .panel .overlay .content-wrapper .booking-progress .spinner {
  margin: 40px auto 0;
  width: 100px;
  text-align: center;
}
.location > .panel .overlay .content-wrapper .booking-progress .spinner > div {
  width: 16px;
  height: 16px;
  background-color: #fff;
  border-radius: 100%;
  display: inline-block;
  animation: sk-bouncedelay 1.7s infinite ease-in-out both;
  margin-right: 5px;
}
.location > .panel .overlay .content-wrapper .booking-progress .spinner .spinner__item1 {
  animation-delay: -0.6s;
}
.location > .panel .overlay .content-wrapper .booking-progress .spinner .spinner__item2 {
  animation-delay: -0.4s;
}
.location > .panel .overlay .content-wrapper .booking-progress .spinner .spinner__item3 {
  animation-delay: -0.2s;
}
@keyframes sk-bouncedelay {
  0%,
  80%,
  100% {
    transform: scale(0);
    opacity: 0;
  }
  40% {
    transform: scale(1);
    opacity: 1;
  }
}
.location > .panel .overlay .content-wrapper .booking-response {
  display: none;
  position: absolute;
  width: 100%;
  top: 50%;
  margin-top: -50px;
}
.location > .panel .overlay .content-wrapper .booking-response.booking-success {
  color: #18bc9c;
}
.location > .panel .overlay .content-wrapper .booking-response.booking-error {
  color: #e74c3c;
}
.location > .panel .panel-body > .panel {
  margin-bottom: 15px;
}
